import React, { useState } from 'react'
import { MdCheckBox, MdCheckBoxOutlineBlank, MdSave } from 'react-icons/md';

interface AddChoiceProps {
  setChoicesValues: Function;
}

export type formErrorInterface = {
  element: string, 
  msg: string
}

export const AddChoice = (props: AddChoiceProps) => {
  const [choiceDescription, setChoiceDescription] = useState<string>("");
  const [choiceStatus, setChoiceStatus] = useState<boolean | null>(null);
  const [formError, setFormError] = useState<formErrorInterface | null>(null);

  const addNewChoice = (submitData: Function) => {
    
    if (choiceDescription == null || choiceDescription === "") {
      return setFormError({
        element: "choiceDescription",
        msg: "Please fill choice"
      });
    }
    if (choiceStatus == null) {
      return setFormError({
        element: "choiceStatus",
        msg: "Please select if it is correct or incorrect"
      });
    }
    submitData({
      choiceId: Math.random(),
      choiceDescription: choiceDescription,
      choiceStatus: choiceStatus
    })
  }

  return (
    <div>
      <div className="p-3 border-t-2 border-b-2 border-gray-200">
        <div className="flex flex-col">
          <label className="font-bold">Answer choice</label>
          <textarea 
          onChange={(e) => {
            setChoiceDescription(e.target.value);
            setFormError(null);
          }}
          value={choiceDescription}
          placeholder="Add a choice" className={`input mt-1 mb-2 font-light text-md ${formError !== null && formError.element === "choiceDescription" && "border-2 border-red-500"}`} style={{minHeight: '120px'}}></textarea>
          {formError !== null && formError.element === "choiceDescription" && (
            <span className="text-red-500 font-bold text-sm">{formError.msg}</span>
          )}
        </div>
      </div>
      <div className="py-2 px-3 flex flex-row justify-between items-center">
        <div className="flex flex-col">
          <div className="flex flex-row my-3 gap-10">
            <span
            onClick={() => {
              setChoiceStatus(true);
              setFormError(null);
            }}
            className={`flex flex-row items-center ${choiceStatus === true ? "text-primary" : "text-gray-700"} cursor-pointer`}>
              {choiceStatus === true ? <MdCheckBox className="text-3xl" /> : <MdCheckBoxOutlineBlank className="text-3xl" />}
              <span className="text-xl">Correct answer</span>
            </span>
            <span 
            onClick={() => {
              setChoiceStatus(false);
              setFormError(null);
            }}
            className={`flex flex-row items-center ${choiceStatus === false ? "text-primary" : "text-gray-700"} cursor-pointer`}>
              {choiceStatus === false ? <MdCheckBox className="text-3xl" /> : <MdCheckBoxOutlineBlank className="text-3xl" />}
              <span className="text-xl">Incorrect answer</span>
            </span>
          </div>
          {formError !== null && formError.element === "choiceStatus" && (
            <span className="text-red-500 font-bold text-sm">{formError.msg}</span>
          )}
        </div>
        <button onClick={() => addNewChoice(props.setChoicesValues)} className="btn bg-primary text-white flex flex-row justify-center items-center font-light px-4 cursor-pointer hover:bg-primary" style={{height: 'fit-content'}}>
          <MdSave className="self-center text-3xl" />
          <span className="text-lg font-light ml-2">Save choice</span>
        </button>
      </div>
    </div>
  )
}
