import React from 'react'
import { FaFilter } from 'react-icons/fa'
import { IoDocumentTextSharp } from 'react-icons/io5'
import { MdCheckBox } from 'react-icons/md'
import { RiSave3Line } from 'react-icons/ri'
import { VscListSelection } from 'react-icons/vsc'

export enum StepsList {
	course = "course",
	questionType = "questionType",
	questionDetails = "questionDetails",
  test = "test",
	save = "save"
}

export interface StepsPropsInterface {
  steps: {
    course: boolean,
    questionType: boolean,
    questionDetails: boolean,
    test: boolean,
    save: boolean
  },
  step: StepsList;
  setStep?: (step:string) => {};
  setCurrentStep: Function;
}

export interface StepsArrayInterface {
  step: StepsList;
  title: string;
  description: string;
  icon: any
}

const Steps = (props: StepsPropsInterface): JSX.Element => {
  const { steps } = props;
  const stepsArray: StepsArrayInterface[] = [
    {
      step: StepsList.course,
      title: "Choose a question category and course",
      description: "Choose a question and a course category",
      icon: MdCheckBox
    },
    {
      step: StepsList.questionType,
      title: "Choose a question type",
      description: "Choose whether the question is multiple, calculated, essay, matching, etc...",
      icon: VscListSelection
    },
    {
      step: StepsList.questionDetails,
      title: "Defining question details",
      description: "Define a question details and the requirements",
      icon: IoDocumentTextSharp
    },
    {
      step: StepsList.test,
      title: "Generate tests",
      description: "Test if you have well prepared the question before submit",
      icon: FaFilter
    },
    {
      step: StepsList.save,
      title: "Save the question to the bank",
      description: "Move the question from the draft to the question bank",
      icon: RiSave3Line
    },
  ];
  const completedStep = (item: StepsArrayInterface, stepsProps: any):boolean => {
    if (item.step === StepsList.course && stepsProps.course === true) {
      return true;
    }
    else if (item.step === StepsList.questionType && stepsProps.questionType === true) {
      return true;
    }
    else if (item.step === StepsList.questionDetails && stepsProps.questionDetails === true) {
      return true;
    }
    else if (item.step === StepsList.test && stepsProps.test === true) {
      return true;
    }
    else if (item.step === StepsList.save && stepsProps.save === true) {
      return true;
    }
    else {
      return false;
    }
  }
  // const [step, setStep] = useState<StepsList>(StepsList.course);
  
  return (
    <div className={`bg-gray-100 ${props.step === StepsList.save ? "border-r-2 border-gray-100" : ""} w-full`}>
      {stepsArray.map((stepItem, i) => {
        return (
          <div key={i + 1} onClick={(): void => props.setCurrentStep(stepItem.step)} className={`flex justify-between items-center cursor-pointer bg-${completedStep(stepItem, steps) === true ? "white border-gray-100" : ""} hover:bg-white ${props.step === stepItem.step ? `border-l-4 border-green-600 ${completedStep(stepItem, steps) === true ? "bg-green-50" : "bg-white"} shadow-md` : ""}`}>
            <div className="flex flex-col p-2 pl-2">
              <span className={`text-${completedStep(stepItem, steps) === true ? "green-600" : "gray-500"} text-md font-bold`}>
                {stepItem.title}
              </span>
              <span className="text-gray-500 font-light" style={{ fontSize: '10px' }}>
                {stepItem.description}
              </span>
            </div>
            <div className={`bg-${completedStep(stepItem, steps) === true ? "green-100" : "gray-200"} rounded-full p-3 mr-2 mt-2 mb-2`}>
              <stepItem.icon className={`h-10 w-10 text-${completedStep(stepItem, steps) === true ? "green-500" : "gray-400"}`} />
            </div>
          </div>
        )
      })}

    </div>
  )
}

export default Steps
