import React from 'react'

export enum QuestionLevel {
	Easy = "Easy",
	Medium = "Medium",
	Hard = "Hard"
}

export interface QuestionLevelComponentInterface {
	currentSelectedLevel: QuestionLevel | null;
	setQuestionLevel: Function;
}

const QuestionLevelComponent = (props: QuestionLevelComponentInterface):JSX.Element => {
  const questionLevelsList = [
    {
      level: QuestionLevel.Easy,
      description: "This question is easy, and will have low grades"
    },
    {
      level: QuestionLevel.Medium,
      description: "This question is medium, and will have medium grades"
    },
    {
      level: QuestionLevel.Hard,
      description: "This question is hard, and will have high grades"
    }
  ];
  return (
    <div className="shadow-md my-3 border rounded pt-1 pb-2 px-2">
      <div className="flex flex-col">
        <span className="text-xl font-bold text-gray-700">Question level {props.currentSelectedLevel && <span className="py-1 px-3 bg-green-500 rounded text-white text-xs float-right mt-1">{props.currentSelectedLevel}</span>}</span>
        <span className="text-xs text-gray-500">How hard does the question can be for correction</span>
      </div>
      <div className="grid md:grid-cols-3 gap-2 mt-3">
        {questionLevelsList && questionLevelsList.map((item, i) => (
          item.level === props.currentSelectedLevel ? (
            <div className="question-level-green">
              <div className="border-4 rounded-full border-green-500 h-12 w-12 col-span-1">
                <div className="border-4 border-white rounded-full bg-green-500 h-10 w-10 col-span-1"></div>
              </div>
              <div className="col-span-3 flex flex-col">
                <span className="font-bold text-green-600 text-xl ml-3">{item.level}</span>
                <span className="text-green-700 text-xs ml-3" style={{fontSize: '9.5px'}}>{item.description}</span>
              </div>
            </div>
            ) : (
            <div className="question-level" onClick={():void => props.setQuestionLevel(item.level)}>
              <div className="border-8 rounded-full border-gray-400 h-12 w-12 col-span-1"></div>
              <div className="col-span-3 flex flex-col">
                <span className="font-bold text-gray-500 text-xl ml-3">{item.level}</span>
                <span className="text-gray-500 text-xs ml-3" style={{fontSize: '9.5px'}}>{item.description}</span>
              </div>
            </div>
          )
        ))
        }
      </div>
    </div>
  );
}

export default QuestionLevelComponent
