import React from 'react'
import Alert, { AlertInterface } from '../../../../components/Alert/Alert';
import { QuestionTypeInterface } from '../QuestionType/QuestionType';
import Matching from './QuestionPreview/Matching';
import MultipleChoice from './QuestionPreview/MultipleChoice';

export interface QuestionTestProps {
  addedQuestion: any;
  selectedQuestionType: QuestionTypeInterface | null;
  setStepStatus: Function;
  setCurrentStep: Function;
}

const QuestionTest = (props: QuestionTestProps) => {
  return (
    <div>
      <Alert title={`${props.addedQuestion.questionTitle}`} description="Preview before putting it in the question bank" theme={AlertInterface.info} />
      {/* Multiple choice question */}
      {props.selectedQuestionType && props.selectedQuestionType.questionTypeId === "1" && (
        <MultipleChoice 
          addedQuestion={props.addedQuestion}
          selectedQuestionType={props.selectedQuestionType}
          setStepStatus={props.setStepStatus}
          setCurrentStep={props.setCurrentStep}
        />
      )}

      {/* Matching question */}
      {props.selectedQuestionType && props.selectedQuestionType.questionTypeId === "3" && (
        <Matching 
          addedQuestion={props.addedQuestion}
          selectedQuestionType={props.selectedQuestionType}
          setStepStatus={props.setStepStatus}
          setCurrentStep={props.setCurrentStep}
        />
      )}
    </div>
  )
}

export default QuestionTest;
