import React, { useState } from 'react'
import { MdCheckBox, MdCheckBoxOutlineBlank, MdSave } from 'react-icons/md';
import { matchingInterface } from './Matching';

interface AddMatchingProps {
  setMatchingValues: (matching: matchingInterface) => void;
}

export type formErrorInterface = {
  element: string, 
  msg: string
}

export const AddMatching = (props: AddMatchingProps) => {
  const [matchingQuestion, setMatchingQuestion] = useState<string>("");
  const [matchingAnswer, setMatchingAnswer] = useState<string>("");
  const [matchingStatus, setMatchingStatus] = useState<boolean | null>(true);
  const [formError, setFormError] = useState<formErrorInterface | null>(null);

  const addNewMatching = (submitData: Function) => {
    
    if (matchingQuestion == null || matchingQuestion === "") {
      return setFormError({
        element: "matchingQuestion",
        msg: "Please fill matching question"
      });
    }
    if (matchingAnswer == null || matchingAnswer === "") {
      return setFormError({
        element: "matchingAnswer",
        msg: "Please fill matching answer"
      });
    }
    if (matchingStatus == null) {
      return setFormError({
        element: "matchingStatus",
        msg: "Please select if it is correct or incorrect"
      });
    }
    submitData({
      matchingId: Math.random(),
      matchingQuestion: matchingQuestion,
      matchingAnswer: matchingAnswer,
      matchingStatus: matchingStatus,
    })
  }

  return (
    <div>
      <div className="p-3 border-t-2 border-b-2 border-gray-200">
        <div className="flex flex-col">
          <label className="font-bold">Matching question</label>
          <textarea 
          onChange={(e) => {
            setMatchingQuestion(e.target.value);
            setFormError(null);
          }}
          value={matchingQuestion}
          placeholder="Add a question" className={`input mt-1 mb-2 font-light text-md ${formError !== null && formError.element === "matchingQuestion" && "border-2 border-red-500"}`} style={{minHeight: '80px'}}></textarea>
          {formError !== null && formError.element === "matchingQuestion" && (
            <span className="text-red-500 font-bold text-sm">{formError.msg}</span>
          )}
        </div>
        <div className="flex flex-col">
          <label className="font-bold">Matching answer</label>
          <textarea 
          onChange={(e) => {
            setMatchingAnswer(e.target.value);
            setFormError(null);
          }}
          value={matchingAnswer}
          placeholder="Add matching answer" className={`input mt-1 mb-2 font-light text-md ${formError !== null && formError.element === "matchingAnswer" && "border-2 border-red-500"}`} style={{minHeight: '80px'}}></textarea>
          {formError !== null && formError.element === "matchingAnswer" && (
            <span className="text-red-500 font-bold text-sm">{formError.msg}</span>
          )}
        </div>
      </div>
      <div className="py-2 px-3 flex flex-row justify-between items-center">
        <div className="flex flex-col">
          <div className="flex flex-row my-3 gap-10">
            <span
            onClick={() => {
              setMatchingStatus(true);
              setFormError(null);
            }}
            className={`flex flex-row items-center ${matchingStatus === true ? "text-primary" : "text-gray-700"} cursor-pointer`}>
              {matchingStatus === true ? <MdCheckBox className="text-3xl" /> : <MdCheckBoxOutlineBlank className="text-3xl" />}
              <span className="text-xl">Correct answer</span>
            </span>
            <span 
            onClick={() => {
              setMatchingStatus(false);
              setFormError(null);
            }}
            className={`flex flex-row items-center ${matchingStatus === false ? "text-primary" : "text-gray-700"} cursor-pointer`}>
              {matchingStatus === false ? <MdCheckBox className="text-3xl" /> : <MdCheckBoxOutlineBlank className="text-3xl" />}
              <span className="text-xl">Incorrect answer</span>
            </span>
          </div>
          {formError !== null && formError.element === "matchingStatus" && (
            <span className="text-red-500 font-bold text-sm">{formError.msg}</span>
          )}
        </div>
        <button onClick={() => addNewMatching(props.setMatchingValues)} className="btn bg-primary text-white flex flex-row justify-center items-center font-light px-4 cursor-pointer hover:bg-primary" style={{height: 'fit-content'}}>
          <MdSave className="self-center text-3xl" />
          <span className="text-lg font-light ml-2">Save choice</span>
        </button>
      </div>
    </div>
  )
}
