import React from 'react'
import { QuestionTypeInterface } from '../QuestionType/QuestionType';
import { StepsList } from '../Steps/Steps';
import Matching from './Matching/Matching';
import MultipleChoice from './MultipleChoice/MultipleChoice';

interface QuestionPaperProps {
  selectedQuestionType: QuestionTypeInterface | null;
  addedQuestion: any;
  setStepStatus: Function;
  setCurrentStep: Function;
  setQuestionData: Function;
}

const QuestionPaper = (props: QuestionPaperProps) => {
  const setQuestionData = (question: any, ) => {
    props.setQuestionData(question);
    props.setStepStatus(StepsList.questionDetails, true);
    props.setCurrentStep(StepsList.test);
  }
  return (
    <div className="py-2 px-2">
      {props.selectedQuestionType !== null && props.selectedQuestionType!.questionTypeId === "1" && (
        <MultipleChoice setQuestionData={setQuestionData} addedQuestion={props.addedQuestion} />
      )}
      {props.selectedQuestionType !== null && props.selectedQuestionType!.questionTypeId === "3" && (
        <Matching setQuestionData={setQuestionData} addedQuestion={props.addedQuestion} />
      )}
    </div>
  )
}

export default QuestionPaper;
