import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { Todo, Auth, fetchTodos, deleteTodo, fetchAuth } from '../../../actions';
import { StoreState } from '../../../reducers';
// import { Link } from "react-router-dom";
import Steps, { StepsList } from './Steps/Steps';
import { CourseInterface } from './Courses/Courses';
import QuestionTypeComponent, { QuestionLevel } from './QuestionLevel/QuestionLevel';
import QuestionCourse from './QuestionCourse/QuestionCourse';
import CourseChapters, { CourseChapterInterface } from './CourseChapters/CourseChapters';
import QuestionType, { QuestionTypeInterface } from './QuestionType/QuestionType';
import QuestionPaper from './QuestionPaper/QuestionPaper';
import QuestionTest from './QuestionTest/QuestionTest';
import SaveQuestion from './SaveQuestion/SaveQuestion';
import Alert, { AlertInterface } from '../../../components/Alert/Alert';
import { MultipleChoiceQuestionInterface } from './QuestionPaper/MultipleChoice/MultipleChoice';

interface AddQuestionState {
	step: StepsList;
	selectedCourse: CourseInterface | null;
	questionLevel: QuestionLevel | null;
	selectedChapters: CourseChapterInterface[] | null;
	setCurrentStep?: Function;
	stepsStatus: { 
		course: boolean, 
		questionType: boolean, 
		questionDetails: boolean, 
		test: boolean, 
		save: boolean 
	},
	selectedQuestionType: QuestionTypeInterface | null;
	setSelectedQuestionType?: Function;
	addedQuestion: any;
	setQuestionData?: Function;
}

interface AddQuestionProps {}

class _AddQuestion extends React.Component<AddQuestionProps, AddQuestionState> {
	constructor(props: AddQuestionProps) {
		super(props);

		this.state = {
			step: StepsList.course,
			selectedCourse: null,
			questionLevel: null,
			selectedChapters: null,
			stepsStatus: { course: false, questionType: false, questionDetails: false, test: false, save: false },
			selectedQuestionType: null,
			addedQuestion: null,
		};
	}

	setQuestionLevel = (level: QuestionLevel): void => {
		this.setState({ questionLevel: level });
	}

	setSelectedCourse = (course: CourseInterface): void => {
		this.setState({ selectedCourse: course });
	}

	selectChapter = (chapter: CourseChapterInterface): void => {
		let currentChapters: CourseChapterInterface[];
		currentChapters = this.state.selectedChapters !== null ? [...this.state.selectedChapters, chapter] : [chapter];
		this.setState({ selectedChapters: currentChapters });
	}

	setStepStatus = (stepItem: StepsList, status: boolean): void => {
		this.setState({ 
			stepsStatus: { 
				course: stepItem === StepsList.course ? status : this.state.stepsStatus.course, 
				questionType: stepItem === StepsList.questionType ? status : this.state.stepsStatus.questionType, 
				questionDetails: stepItem === StepsList.questionDetails ? status : this.state.stepsStatus.questionDetails, 
				test: stepItem === StepsList.test ? status : this.state.stepsStatus.test, 
				save: stepItem === StepsList.save ? status : this.state.stepsStatus.save 
			} 
		});
	}

	removeChapter = (chapter: CourseChapterInterface): void => {
		if (this.state.selectedChapters!.indexOf(chapter) === -1) {
      return;
    }
    const deletedData: CourseChapterInterface | undefined = this.state.selectedChapters!.find((itm) => itm === chapter);
		this.setState({ selectedChapters: this.state.selectedChapters!.filter((item) => item !== deletedData) });
	}

	setCurrentStep = (step: StepsList) => {
		this.setState({ step: step });
	}
	setSelectedQuestionType = (type: QuestionTypeInterface) => {
		this.setState({ selectedQuestionType: type });
	}

	setQuestionData = (question: MultipleChoiceQuestionInterface) => {
		this.setState({ addedQuestion: question });
	}

	render() {		
		return (
			<Fragment>
				<div className="">
					<div className="bg-hero mt-5 rounded p-4 flex justify-between items-center">
						<div className="flex flex-col">
							<span className="text-white text-4xl">Create new question {this.state.selectedQuestionType !== null && <span className="text-lg font-extrabold"> - ({this.state.selectedQuestionType.questionTypeTitle})</span>}</span>
							<span className="text-gray-200 font-light text-sm mt-1">
								{this.state.selectedCourse && <span className="font-bold">{this.state.selectedCourse!.courseName}</span>}{this.state.questionLevel && <span className="font-bold">-({this.state.questionLevel})</span>}
								{!this.state.selectedCourse && <span>The question will be stored into the question bank</span>}
							</span>
						</div>
						<div>
							<span className="text-white text-xl font-light text pr-5">
								{!this.state.selectedCourse && <span>Choose the question category and course</span>}
							</span>
						</div>
					</div>

					<div className="my-4 grid md:grid-cols-3">
						<div className="md:col-span-1 md:flex min-h-full">
              <Steps steps={this.state.stepsStatus} setCurrentStep={this.setCurrentStep} step={this.state.step} />
						</div>
						<div className="bg-white md:col-span-2 p-1 pb-4" style={{height: 'fit-content', minHeight: '400px'}}>
							{this.state.step === StepsList.course && (
								<>
									{/* Select question courses */}
									<QuestionCourse selectedCourse={this.state.selectedCourse} setSelectedCourse={this.setSelectedCourse} />
									{/* Question level */}
									{this.state.selectedCourse !== null && (
										<QuestionTypeComponent currentSelectedLevel={this.state.questionLevel} setQuestionLevel={this.setQuestionLevel} />
									)}
									{/* Select course chapters */}
									{this.state.questionLevel !== null && (
										<CourseChapters currentChapters={this.state.selectedChapters} selectChapter={this.selectChapter} removeChapter={this.removeChapter} setCurrentStep={this.setCurrentStep} setStepStatus={this.setStepStatus} />
									)}
								</>
							)}

							{this.state.step === StepsList.questionType && (
								this.state.selectedChapters === null || (this.state.selectedChapters && this.state.selectedChapters!.length === 0) ? (
									<Alert title="Error" description="Please complete the previous step by selecting course and chapter" theme={AlertInterface.danger} />
								) : (
									<QuestionType selectedQuestionType={this.state.selectedQuestionType} setSelectedQuestionType={this.setSelectedQuestionType} setStepStatus={this.setStepStatus} setCurrentStep={this.setCurrentStep} />
								)
							)}

							{this.state.step === StepsList.questionDetails && (
								this.state.selectedQuestionType !== null ? (
									<QuestionPaper selectedQuestionType={this.state.selectedQuestionType} setStepStatus={this.setStepStatus} setCurrentStep={this.setCurrentStep} setQuestionData={this.setQuestionData} addedQuestion={this.state.addedQuestion} />
								) : (
									<Alert title="Error" description="Please complete the previous step by selecting question type" theme={AlertInterface.danger} />
								)
							)}

							{this.state.step === StepsList.test && (
								!this.state.addedQuestion ? (
									<Alert title="Error" description="Please complete the previous step by defining question details" theme={AlertInterface.danger} />
								) : (
									<QuestionTest addedQuestion={this.state.addedQuestion} setStepStatus={this.setStepStatus} setCurrentStep={this.setCurrentStep} selectedQuestionType={this.state.selectedQuestionType} />
								)
							)}
							{this.state.step === StepsList.save && (
								!this.state.addedQuestion || this.state.addedQuestion === null ? (
									<Alert title="Error" description="Please complete the previous step by defining question details" theme={AlertInterface.danger} />
								) : (
									<SaveQuestion questionLevel={this.state.questionLevel} selectedCourse={this.state.selectedCourse} selectedChapters={this.state.selectedChapters} selectedQuestionType={this.state.selectedQuestionType} addedQuestion={this.state.addedQuestion} setStepStatus={this.setStepStatus} setCurrentStep={this.setCurrentStep} />
								)
							)}
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

const mapStateToProps = ({ todos, auth }: StoreState): { todos: Todo[]; auth: Auth } => {
	return { todos: todos, auth: auth };
};

export const AddQuestion = connect(mapStateToProps, {
	fetchTodos,
	deleteTodo,
	fetchAuth
})(_AddQuestion);
