import React from 'react'
import { MdArrowForward, MdCheckBoxOutlineBlank, MdRadioButtonUnchecked } from 'react-icons/md';
import { choiceInterface } from '../../QuestionPaper/MultipleChoice/MultipleChoice';
import { StepsList } from '../../Steps/Steps';
import { QuestionTestProps } from '../QuestionTest';

const MultipleChoice = (props: QuestionTestProps) => {
  return (
    <div className="p-3 flex flex-col">
      <span className="font-bold text-lg">{props.addedQuestion.questionDescription}</span>
      <span className="text-gray-400 text-xs mb-4 italic">Choose the correct answer(s)</span>
      
      <div className="py-4" style={{borderTop: '1px solid #cecece'}}>
        {props.addedQuestion && props.addedQuestion.questionChoices.map((item: any, i:number) => (
          <div className="flex flex-row text-gray-500 cursor-pointer hover:text-black hover:font-bold py-2">
            <span className="mr-3">
              {props.addedQuestion.questionChoices.filter((item: choiceInterface) => item.choiceStatus === true).length > 1 ? (
                <MdCheckBoxOutlineBlank className="text-2xl" /> 
              ) : (
                <MdRadioButtonUnchecked className="text-2xl" /> 
              )}
            </span>
            <span className="font-bold">{item.choiceDescription}</span>
          </div>
        ))}
      </div>

      <div>
        <button onClick={() => { props.setCurrentStep(StepsList.save); props.setStepStatus(StepsList.test, true) }} className="btn bg-primary text-white flex flex-row justify-center items-center font-light px-4 cursor-pointer hover:bg-primary float-right" style={{height: 'fit-content'}}>
          <span className="text-lg font-light mr-2">Continue</span>
          <MdArrowForward className="text-3xl" />
        </button>
      </div>

    </div>
  )
}

export default MultipleChoice
